
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import techRecords from '@/data/tech'

export default {
components: {
Group,
Cell,
XHeader,
Spinner
},
created() {
this.getImage()
},
watch: {
'$route': 'getImage'
},
data () {
return {
techRecords: techRecords,
currentRecord: '',
currentSrc: '',
title: '',
errAlt: ''
}
},
methods: {
getImage() {
for(let item in this.techRecords) {
let obj = this.techRecords[item]
if(this.$route.params.key == obj.value) {
setTimeout(() => {
this.currentRecord = obj
this.loadImage(obj.link)
this.title = obj.name
}, 1000)
}
}
},
loadImage(src) {
let newImg = new Image()
newImg.src = src
newImg.onerror = () => {
this.errAlt = '请刷新页面重试'
}
newImg.onload = () => {
this.currentSrc = newImg.src
}
}
}
}



